@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

body {
    background-color: #0b0b0b;
    font-family: 'Roboto Mono', monospace;
}

.circle {
    height: 50px;
    width: 100px;
    background: linear-gradient(267.49deg, rgba(100, 206, 19, 0.4), rgba(91, 35, 225, .25));
    border-radius: 5px;
    display: grid;
    place-items: center;
    border: .5px solid silver;
    cursor: pointer;
    color: silver;
}

.lightt {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    display: grid;
    place-items: center;
    border: .5px solid silver;
    cursor: pointer;
}

.square {
    height: 30vh;
    width: 20vw;
    align-self: center;
    background-color: #1c1c1c;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;

}

.rect {
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 5px;
    /* height: ; */
    width: 25vw;
    background-color: #1c1c1c;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 10px;
    border: 1px solid #8aec4e;
    overflow: hidden;
}

.silver {
    color: silver
}

.twitter {
    background-color: #1c1c1c;
    color: #8aec4e;
    cursor: pointer;
}

.toflex {
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.marginleft {
    /* margin-left:30px; */
    margin-top: 40px;
    margin-bottom: -40px;
    color: silver
}

.likeaccor {
    background-color: #0b0b0b;
    border: 1px solid #8aec4e;
    width: 70vw;

}

.flexin {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    display: flex;
    gap: 10px
}

.width {
    width: 70vw;
    border: 1px solid #8aec4e;
    background-color: #0b0b0b;
    color: silver;
    border-radius:0px;
}

.flexit {
    display: flex;
    gap: 30px;
    font-size: 14px;
    margin-right: 40vh;
    margin-left: 30vh;
    margin-top: 15vh;
    margin-bottom: 10vh;
    cursor: pointer;
  }

